import {
	mapGetters,
	mapMutations
} from "vuex"
export default {
	data() {
		return {
			logistics: '',
			disabled: true
		};
	},
	computed: {
		...mapGetters({
			getReturnOrder: 'getReturnOrder'
		})
	},
	onLoad() {
		console.log(this.getReturnOrder);
	},
	methods: {
		// 退货
		Return(){
			console.log(this.disabled)
			if(this.disabled == true){
				return
			}
			let data ={
				order_id:this.getReturnOrder.order_id,
				type:2,
				express_number:this.logistics
			}
			this.$api.orderRefund(data).then(res=>{
					if(res.data.errno == 0){
						uni.showToast({
							title: '退款申请提交成功',
							duration: 1000,
							icon: "none"
						});
						setTimeout(res => {
							uni.switchTab({
								url: '/pages/order/order'
							})
						}, 1000)
					}else{
						this.close('refund');
						uni.showToast({
							title: '退款申请提交失败',
							duration: 1000,
							icon: "none"
						});
					}
			})
		},
		// 粘贴板
		copye() {
			uni.setClipboardData({
				data: this.getReturnOrder.order_number,
				success: function() {
					console.log('success');
					uni.showToast({
						title: '复制成功',
						duration: 1000,
						icon: "none"
					});
				}
			});
		},
		getLogistics(e) {
			this.logistics = e.detail.value
			if (this.logistics !== '') {
				this.disabled = false
			} else {
				this.disabled = true
			}
		}
	}
}
